<template>
  <v-container fluid >
    <v-layout row class="pl-3 pt-2">
      <v-flex xs5>
        <v-text-field color="purple" v-model="requester.name" label="用户名" prepend-icon="account_box" required :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5 ><v-btn outline color="purple" :disabled="editInfo" @click="changePwd=true"><v-icon left>lock</v-icon>修改密码</v-btn></v-flex>
    </v-layout>
    <v-layout row justify-space-between class="pl-3 pt-3">
      <v-flex xs5>
        <v-menu
          ref="menu"
          :close-on-content-click="false"
          v-model="dateMenu"
          :nudge-right="40"
          lazy
          transition="scale-transition"
          offset-y
          full-width
          min-width="290px"
          :disabled="editInfo"
        >
          <v-text-field
            slot="activator"
            v-model="requester.birth"
            label="生日"
            prepend-icon="event"
            readonly
            required
            color="purple"
            :disabled="editInfo"
          ></v-text-field>
          <v-date-picker
            ref="picker"
            v-model="requester.birth"
            :max="new Date().toISOString().substr(0, 10)"
            min="1950-01-01"
            @change="save(requester.birth)"
            color="purple"
          ></v-date-picker>
        </v-menu>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-radio-group v-model="requester.sex" row label="性别" :disabled="editInfo">
          <v-radio color="purple" label="男性" value="0" :disabled="editInfo"></v-radio>
          <v-radio color="purple" label="女性" value="1" :disabled="editInfo"></v-radio>
        </v-radio-group>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-text-field v-model="requester.email" color="purple" label="邮箱" prepend-icon="email" type="email" required :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-text-field v-model="requester.company" color="purple" prepend-icon="card_travel" label="公司" :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row class="pl-3">
      <v-flex xs5>
        <v-text-field v-model="requester.occupation" color="purple" prepend-icon="turned_in" label="职务" :disabled="editInfo"></v-text-field>
      </v-flex>
    </v-layout>
    <v-speed-dial v-model="fab" fixed bottom right direction="top" open-on-hover transition="slide-y-reverse-transition">
      <v-btn slot="activator" v-model="fab" color="orange" dark fab>
        <v-icon class="pt-3">build</v-icon>
        <v-icon class="pt-3">close</v-icon>
      </v-btn>
      <v-btn  fab dark small color="green" @click="editInfo=!editInfo">
        <v-icon class="pt-2">edit</v-icon>
      </v-btn>
      <v-btn fab dark small color="indigo">
        <v-icon class="pt-2">clear</v-icon>
      </v-btn>
      <v-btn fab dark small color="red">
        <v-icon class="pt-2">undo</v-icon>
      </v-btn>
    </v-speed-dial>
    <v-btn color="purple darken-1" dark class="mb-5 ml-3" :disabled="editInfo">提交修改</v-btn>
    <v-dialog v-model="changePwd" max-width="500px">
      <v-card>
        <v-card-title class="title">
          密码重设
        </v-card-title>
        <v-card-text class="pt-0">
          <v-text-field v-model="oldPwd" color="purple"  label="旧密码" required></v-text-field>
          <v-text-field v-model="newPwd" color="purple"  label="新密码" required></v-text-field>
          <v-text-field v-model="newPwdCheck" color="purple"  label="新密码确认" required></v-text-field>
        </v-card-text>
        <v-card-actions class="pt-0">
          <v-spacer></v-spacer>
          <v-btn color="purple" flat @click.stop="changePwd=false">确认</v-btn>
          <v-btn color="purple" flat @click.stop="changePwd=false">放弃</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
    export default {
        name: "PersonalInfo",
      data() {
          return {
            editInfo: true,
            changePwd: false,
            oldPwd: '',
            newPwd: '',
            newPwdCheck: '',
            requester: {
              name: 'Jeffery',
              key: '',
              birth: '1997-01-01',
              sex: '0',
              email: 'www@sina.com',
              occupation: '总经理',
              company: 'ABCD有限公司'
            },
          }
      }
    }
</script>

<style scoped>

</style>



