<template>
  <v-app id="inspire">
    <v-toolbar color="purple darken-2" dark fixed app clipped-right id="toolbar">
      <v-toolbar-title>{{barTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
      <v-btn slot="activator" icon large target="_blank">
        <v-icon large>reply</v-icon>
      </v-btn>
        <span>退出</span>
      </v-tooltip>
    </v-toolbar>
    <v-navigation-drawer :mini-variant.sync="mini" v-model="drawer" stateless hide-overlay mini-variant-width="85" app class="grey lighten-4">
      <v-toolbar flat >
        <v-list class="pa-0">
          <v-list-tile avatar>
            <v-list-tile-avatar size="52px" class="">
              <img src="https://randomuser.me/api/portraits/men/85.jpg" >
            </v-list-tile-avatar>
            <v-list-tile-content>
              <v-list-tile-title>{{requesterName}}</v-list-tile-title>
              <v-list-tile-sub-title>会员等级：{{requesterRank}}</v-list-tile-sub-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-btn icon @click.native.stop="mini = !mini">
                <v-icon>chevron_left</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-toolbar>
      <v-divider dark class="my-0"></v-divider>
      <v-list dense>
        <template v-for="(item, i) in items">
          <v-divider v-if="item.divider" :key="i" dark class="my-3"></v-divider>
          <v-list-tile v-else :key="i" :to="item.path" @click="barTitle=item.text">
            <v-list-tile-action>
              <v-icon class="purple--text text--darken-2">{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title class="grey--text">
                {{ item.text }}
              </v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
        </template>
      </v-list>
    </v-navigation-drawer>
    <v-content>
      <router-view/>
    </v-content>
    <v-footer class="purple darken-2" id="footer" app>
      <v-layout row wrap align-center>
        <v-flex xs12 text-xs-center>
          <div class="white--text ml-3">
            Made with
            <v-icon class="red--text">favorite</v-icon>
            by AlphaCat Team
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
    export default {
        name: "RequesterIndex",
      data: () => ({
        drawer: true,
        mini: false,
        barTitle: "任务中心",
        requesterName: sessionStorage.getItem('name'),
        requesterRank: sessionStorage.getItem('type'),
        items: [
          { icon: 'apps', text: '任务中心',  path: '/requester/tasks'},
          { icon: 'notifications', text: '系统邮件', path: '/requester/tasks' },
          { icon: 'delete', text: '回收站',  path: '/requester/tasks' },
          { icon: 'gesture', text: '草稿箱',  path: '/requester/tasks' },
          { divider: true },
          { icon: 'face', text: '个人信息', path: '/requester/personalInfo'},
          { icon: 'group', text: '会员中心', path: '/requester/tasks' },
          { divider: true },
          { icon: 'help', text: '发布者指南', path: '/requester/tasks' },
        ]
      }),
      props: {
        source: String
      }
    }
</script>

<style scoped>
  #footer{
    opacity: 0.8;
  }
</style>



