<template>
  <v-app id="idleTask">
    <v-toolbar color="purple darken-2" dark fixed app clipped-right id="toolbar">
      <v-toolbar-title>{{barTitle}}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-tooltip bottom>
        <v-btn slot="activator" icon large target="_blank"><v-icon large>home</v-icon></v-btn>
        <span>返回主页</span>
      </v-tooltip>
    </v-toolbar>
    <v-content fluid fill-height >
      <v-card width="100%">
        <v-layout row align-center justify-center>
          <v-flex xs8>
            <v-card flat>
              <v-card-text>
                <v-container fluid>
                  <v-layout row>
                    <v-flex xs4><v-subheader>任务名称</v-subheader></v-flex>
                    <v-flex xs8><v-text-field name="taskName" label="Task Name" :value="taskInfo.name" :disabled="edit" color="purple"></v-text-field></v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs4><v-subheader>任务类型</v-subheader></v-flex>
                    <v-flex xs8><v-text-field name="taskType" label="Task Type" :value="taskType" disabled></v-text-field></v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs4><v-subheader>任务描述</v-subheader></v-flex>
                    <v-flex xs8><v-text-field name="taskDescription" label="Task Description" :value="taskInfo.description" :disabled="edit" color="purple"></v-text-field></v-flex>
                  </v-layout>
                  <v-divider v-if="taskInfo.method===1" class="my-3"></v-divider>
                  <v-layout v-if="taskInfo.method===1" row>
                    <v-checkbox label="是否整体描述" v-model="taskInfo.hasWholeLabel" color="purple" :disabled="edit" class="pt-2"></v-checkbox>
                  </v-layout>
                  <v-divider></v-divider>
                  <v-subheader v-if="taskInfo.method===1" class="pl-1 pt-4">任务标签及预设选项</v-subheader>
                  <v-container v-if="taskInfo.method===1" fluid grid-list-sm class="pt-3">
                    <v-layout v-for="(tag,index) in taskInfo.labels" :key="tag.label" row wrap>
                      <v-select v-model="tag.choices" :label="tag.label" chips tags solo prepend-icon="bookmark_border"
                                append-icon=" " clearable color="purple" style="background: transparent" pd-1 :disabled="edit">
                        <template slot="selection" slot-scope="data">
                          <v-chip :selected="data.selected" close @input="remove(data.item,index)">
                            <strong>{{ data.item }}</strong>&nbsp;
                            <span>({{tag.label}})</span>
                          </v-chip>
                        </template>
                      </v-select>
                      <v-btn outline small fab color="purple" :disabled="edit" @click="removeLabel(index)" >
                        <v-icon class="pt-2">delete_forever</v-icon>
                      </v-btn>
                    </v-layout>
                  </v-container>
                  <v-layout v-if="taskInfo.method===1" row class="pr-5 pl-3">
                    <v-text-field :rules="[(v) => v.length <= 10 || 'Max 10 characters']" :counter="10" prepend-icon="bookmark"
                                  id="newLabelField" name="newLabelField" label="新标签名" color="purple" v-model="newLabelName"
                                  ref="newLabelField" :disabled="edit"></v-text-field>
                    <v-btn round color="purple" outline :disabled="edit" @click="addNewLabel">添加标签</v-btn>
                  </v-layout>
                  <v-divider class="my-3"></v-divider>
                  <v-layout row>
                    <v-flex xs7  pt-3 pb-3>
                      <el-upload class="upload-demo" action="https://jsonplaceholder.typicode.com/posts/" :on-preview="handlePreview"
                                 :on-remove="handleRemove" :file-list="taskInfo.picList" list-type="picture" :disabled="edit">
                        <el-button size="small" type="primary" :disabled="edit">点击上传</el-button>
                        <div class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
                      </el-upload>
                    </v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs4><v-slider v-model="taskInfo.creditPerPic" color="purple" label="每张图片奖励" hint="Be careful" min="1" max="10" thumb-label :disabled="edit"></v-slider></v-flex>
                    <v-flex xs2><v-text-field v-model="taskInfo.creditPerPic" type="number" prepend-icon="monetization_on" color="purple" :disabled="edit"></v-text-field></v-flex>
                  </v-layout>
                  <v-layout row>
                    <v-flex xs6>
                      <v-slider v-model="taskInfo.creditFinished" color="purple" label="完成任务奖励" hint="Be careful" min="1" max="200" thumb-label :disabled="edit"></v-slider>
                    </v-flex>
                    <v-flex xs2><v-text-field v-model="taskInfo.creditFinished" type="number" prepend-icon="monetization_on" color="purple" :disabled="edit"></v-text-field></v-flex>
                  </v-layout>
                  <v-divider class="my-3"></v-divider>
                  <v-layout row>
                    <v-flex  xs6 pl-4 pt-2>
                      <v-menu ref="s_menu" :close-on-content-click="false" v-model="s_menu" :nudge-right="40"
                              :return-value.sync="taskInfo.startTime" lazy transition="scale-transition" offset-y
                              full-width min-width="290px">
                        <v-text-field slot="activator" v-model="taskInfo.startTime" label="任务开始日期" prepend-icon="event"
                                      readonly color="purple" :disabled="edit"></v-text-field>
                        <v-date-picker v-model="taskInfo.startTime" no-title scrollable>
                          <v-spacer></v-spacer>
                          <v-btn flat color="primary" @click="s_menu = false">Cancel</v-btn>
                          <v-btn flat color="primary" @click="$refs.s_menu.save(taskInfo.startTime)">OK</v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-flex>
                    <v-flex  xs6 pl-4 pt-2>
                      <v-menu ref="e_menu" :close-on-content-click="false" v-model="e_menu" :nudge-right="40"
                              :return-value.sync="taskInfo.endTime" lazy transition="scale-transition" offset-y
                              full-width min-width="290px">
                        <v-text-field slot="activator" v-model="taskInfo.endTime" label="任务结束日期" prepend-icon="event"
                                      readonly color="purple" :disabled="edit"></v-text-field>
                        <v-date-picker v-model="taskInfo.endTime" no-title scrollable>
                          <v-spacer></v-spacer>
                          <v-btn flat color="primary" @click="e_menu = false">Cancel</v-btn>
                          <v-btn flat color="primary" @click="$refs.e_menu.save(taskInfo.endTime)">OK</v-btn>
                        </v-date-picker>
                      </v-menu>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card-text>
              <v-speed-dial v-model="fab" fixed bottom right direction="top" open-on-hover transition="slide-y-reverse-transition">
                <v-btn slot="activator" v-model="fab" color="orange" dark fab>
                  <v-icon class="pt-3">build</v-icon>
                  <v-icon class="pt-3">close</v-icon>
                </v-btn>
                <v-btn  fab dark small color="green" @click="edit=!edit">
                  <v-icon class="pt-2">edit</v-icon>
                </v-btn>
                <v-btn fab dark small color="indigo" @click="clearContent">
                  <v-icon class="pt-2">clear</v-icon>
                </v-btn>
                <v-btn fab dark small color="red" @click="recoverContent">
                  <v-icon class="pt-2">undo</v-icon>
                </v-btn>
              </v-speed-dial>
              <v-btn color="purple darken-1" dark class="mb-5">更新发布<v-icon right>pan_tool</v-icon></v-btn>
              <v-btn color="purple lighten-1" dark class="mb-5">存为草稿<v-icon right >gesture</v-icon></v-btn>
              <v-btn color="grey" dark class="mb-5">删除任务<v-icon right >delete</v-icon></v-btn>
            </v-card>
          </v-flex>
        </v-layout>
      </v-card>
    </v-content>
    <v-footer class="purple darken-2" id="footer" app>
      <v-layout row wrap align-center>
        <v-flex xs12 text-xs-center>
          <div class="white--text ml-3">
            Made with
            <v-icon class="red--text">favorite</v-icon>
            by AlphaCat Team
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
    export default {
      name: "IdleTask",
      data() {
        return{
          taskId: this.$route.params.taskId ,
          barTitle: '任务详情——未开始任务',
          taskType: '单框标注',
          taskState: 'idle',
          edit: true,
          originalTaskInfo: {
            id: '',
            requesterId: '',
            name: 'myTask',
            description: 'abcdefghijklmn',
            creditPerPic: '2',
            creditFinished: '36',
            picList: [
              {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
              {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}
            ],
            method: 1,
            hasWholeLabel: true,
            startTime: '2018-06-01',
            endTime: '2018-07-01',
            labels: [
              {label: 'sex', choices: ['male','female']},
              {label: 'color', choices:['red','blue','pink']}
            ],
          },
          taskInfo: {
            id: '',
            requesterId: '',
            name: 'myTask',
            description: 'abcdefghijklmn',
            creditPerPic: '2',
            creditFinished: '36',
            picList: [
              {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
              {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}
            ],
            method: 1,
            hasWholeLabel: true,
            startTime: '2018-06-01',
            endTime: '2018-07-01',
            labels: [
              {label: 'sex', choices: ['male','female']},
              {label: 'color', choices:['red','blue','pink']}
            ],
          },
          newLabelName: '',
          s_menu: false,
          e_menu: false,
          fab: false,
        }
      },
      methods: {
        removeLabel(index){
          this.taskInfo.labels.splice(index,1);
          this.taskInfo.labels=[...this.taskInfo.labels]
        },
        addNewLabel(){
          if (this.$refs.newLabelField.value!=='') {
            let obj={};
            obj.label=this.$refs.newLabelField.value;
            obj.choices=[];
            this.taskInfo.labels.push(obj);
            this.taskInfo.labels=[...this.taskInfo.labels];
            this.$refs.newLabelField.reset();
          }
        },
        clearContent() {
        },
        recoverContent(){

        },
        create() {
          console.log('taskid: ' + this.$route.params.taskId);
        }
      }
  }
</script>

<style scoped>
  #footer{
    opacity: 0.8;
  }
</style>



