<template>
  <v-app>
    <v-toolbar color="purple darken-2" dark fixed app clipped-right id="toolbar">
    <v-toolbar-title>{{barTitle}}</v-toolbar-title>
    <v-spacer></v-spacer>
    <v-tooltip bottom>
      <v-btn slot="activator" icon large target="_blank"><v-icon large>home</v-icon></v-btn>
      <span>返回主页</span>
    </v-tooltip>
  </v-toolbar>
    <v-content>
      <v-container fluid>
        <v-layout row align-center justify-center>
          <v-flex xs10>
            <div slot="header" class="title purple--text pt-3 pb-1">任务信息</div>
            <v-divider ></v-divider>
            <v-layout row class="pl-5 pt-3">
              <v-flex xs4 ><div class="body-2">任务名称：&emsp;&emsp;{{taskInfo.name}}</div></v-flex>
              <v-flex xs4><div class="body-2">任务类型：&emsp;&emsp;{{taskType}}</div></v-flex>
              <v-flex xs1><div class="body-2">任务进度:</div></v-flex>
              <v-flex xs3><v-progress-circular :value="taskInfo.taskProgressRate" color="purple" size="24"></v-progress-circular></v-flex>
            </v-layout>
            <v-layout row class="pl-5">
              <v-flex xs8><v-text-field name="taskDescription" label="任务描述" textarea v-model="taskInfo.description" :disabled="editDescription" color="purple" rows="3"></v-text-field></v-flex>
              <v-flex xs4 class="pt-3 pl-2"><v-btn fab dark small class="text-xs-center" color="purple" @click="editDescription=!editDescription"><v-layout align-center justify-center><v-icon dark>edit</v-icon></v-layout></v-btn></v-flex>
            </v-layout>
            <v-layout v-if="taskInfo.method===1" row class="pl-5"><v-flex xs12><div class="body-2">任务需求: &emsp;&emsp;{{taskInfo.hasWholeLabel ? "需要整体描述" : "无需整体描述"}}</div></v-flex></v-layout>
            <v-layout v-if="taskInfo.method===1" row class="pl-5 pt-4">
              <v-flex xs2><div class="body-2">任务标签及预设选项:</div></v-flex>
            </v-layout>
            <v-layout v-if="taskInfo.method===1" row class="pl-5 pt-2">
              <v-flex xs4>
                <v-list style="background: transparent" class="elevation-0">
                  <v-list-group v-for="item in taskInfo.labels" :key="item.label" no-action>
                    <v-list-tile slot="activator" color="purple">
                      <v-list-tile-action>
                        <v-icon color="purple">label</v-icon>
                      </v-list-tile-action>
                      <v-list-tile-content>
                        <v-list-tile-title>{{ item.label }}</v-list-tile-title>
                      </v-list-tile-content>
                    </v-list-tile>
                    <v-list-tile v-for="subItem in item.choices" :key="subItem">
                      <v-list-tile-content>
                        <v-list-tile-title>{{ subItem}}</v-list-tile-title>
                      </v-list-tile-content>
                    </v-list-tile>
                  </v-list-group>
                </v-list>
              </v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-4"><v-flex xs2><div class="body-2">任务图片:</div></v-flex></v-layout>
            <v-layout row class="pl-5 pt-2">
              <v-flex xs8>
                <v-card style="max-height: 200px" class="scroll-y">
                  <v-container grid-list-sm fluid>
                    <v-layout row wrap>
                      <v-flex v-for="pic in taskInfo.picList" :key="pic.name" xs2>
                        <v-card flat tile>
                          <v-card-media :src="`https://unsplash.it/150/300?image=${Math.floor(Math.random() * 100) + 1}`" height="100px"></v-card-media>
                        </v-card>
                      </v-flex>
                    </v-layout>
                  </v-container>
                </v-card>
              </v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-5">
              <v-flex xs4><v-text-field v-model="taskInfo.startTime" label="任务开始日期" prepend-icon="event" readonly color="purple" disabled="true"></v-text-field></v-flex>
              <v-flex xs4 class="pl-5">
                <v-menu ref="e_menu" :close-on-content-click="false" v-model="e_menu" :nudge-right="40" :return-value.sync="taskInfo.endTime"
                      lazy transition="scale-transition" offset-y full-width min-width="290px">
                <v-text-field slot="activator" v-model="taskInfo.endTime" label="任务结束日期" prepend-icon="event" readonly color="purple" :disabled="editEndTime"></v-text-field>
                <v-date-picker v-model="taskInfo.endTime" no-title scrollable>
                  <v-spacer></v-spacer>
                  <v-btn flat color="primary" @click="e_menu = false">Cancel</v-btn>
                  <v-btn flat color="primary" @click="$refs.e_menu.save(taskInfo.endTime)">OK</v-btn>
                </v-date-picker>
              </v-menu>
              </v-flex>
              <v-flex xs4 class="pl-2"><v-btn fab dark small class="text-xs-center" color="purple" @click="editEndTime=!editEndTime"><v-layout align-center justify-center><v-icon dark>edit</v-icon></v-layout></v-btn></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-1">
              <v-btn color="purple" dark><v-icon left>pan_tool</v-icon>发布更改</v-btn>
            </v-layout>
            <div slot="header" class="title purple--text pt-3 pb-1 ">任务标注情况</div>
            <v-divider ></v-divider>
            <v-layout row class="pl-5 pt-3">
              <v-flex xs4 ><div class="body-2">参与任务人数：&emsp;&emsp;{{taskInfo.workerCount}}</div></v-flex>
              <v-flex xs4><div class="body-2">图片标注率：&emsp;&emsp;{{taskInfo.tagRate}}</div></v-flex>
            </v-layout>
            <v-layout row class="pl-5 pt-3 pb-5">
              <v-flex xs4 ><div class="body-2">未标注图片数：&emsp;&emsp;{{taskInfo.picUndone}}</div></v-flex>
              <v-flex xs4><div class="body-2">已消耗积分：&emsp;&emsp;{{taskInfo.costCredit}}</div></v-flex>
            </v-layout>
          </v-flex>
        </v-layout>
      </v-container>
    </v-content>
    <v-footer class="purple darken-2" id="footer" app>
      <v-layout row wrap align-center>
        <v-flex xs12 text-xs-center>
          <div class="white--text ml-3">
            Made with
            <v-icon class="red--text">favorite</v-icon>
            by AlphaCat Team
          </div>
        </v-flex>
      </v-layout>
    </v-footer>
  </v-app>
</template>

<script>
    export default {
        name: "UnderwayTask",
      data(){
          return{
            barTitle: '任务详情——进行中任务',
            taskType: '单框标注',
            editDescription: true,
            editEndTime: true,
            e_menu: false,
            taskInfo: {
              id: '',
              requesterId: '',
              name: 'myTask',
              description: 'abcdefghijklmn',
              creditPerPic: '2',
              creditFinished: '36',
              picList: [
                {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
                {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'},
                {name: 'pic1', url: ''},
                {name: 'pic2', url: ''},
                {name: 'pic3', url: ''},
                {name: 'pic4', url: ''},
                {name: 'pic5', url: ''},
                {name: 'pic6', url: ''},
                {name: 'pic7', url: ''},
                {name: 'pic8', url: ''},
                {name: 'pic9', url: ''},
                {name: 'pic10', url: ''},
                {name: 'pic11', url: ''},
                {name: 'pic12', url: ''},
                {name: 'pic13', url: ''},
                {name: 'pic14', url: ''},
                {name: 'pic15', url: ''},
              ],
              method: 1,
              hasWholeLabel: true,
              startTime: '2018-06-01',
              endTime: '2018-07-01',
              labels: [
                {label: 'sex', choices: ['male','female']},
                {label: 'color', choices:['red','blue','pink']}
              ],
              taskProgressRate:30,
              workerCount: 12,
              tagRate: "20%",
              picUndone: 4,
              costCredit: 134,
            },
          }
      }
    }
</script>

<style scoped>
  #footer{
    opacity: 0.8;
  }
</style>



